*
{
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
header
{
  background-color: rgb(182, 14, 106);
  color: white;
  text-align: center;
  position: fixed;
  width: 100%;
  z-index: 1;
}
nav
{
  background-color: skyblue;
  width:30%;
  height: 100vh;
  float: left;
  text-align: center;
  padding-top: 100px;
}
#btn,#addBtn
{
  background-color: rgb(245, 208, 0);
  width:300px;
  height: 50px;
  margin-top: 10px; 
  text-align: center;
  border: none;
}
#addBtn
{
  background-color: rgb(37, 12, 145);
  color: white;
}

article
{
  width:70%;
  height: 100vh;
  background-color: rgb(43, 41, 41);
  float: left;
  position: relative;
}
.inputfield
{
  position: absolute;
  top:50%;
  left: 50%;
  transform: translate(-50%,-50%);
}
#title
{
  width:500px;
  height: 50px;
}
#save,#delete
{
  width:200px;
  height: 50px;
  
  color: white;
  border: none;
}
#save
{
  background-color: rgb(9, 204, 113);
}
#delete
{
  background-color: rgb(224, 61, 88);
  margin-top: 20px;
}
#notes
{
  height: 300px;
  width:500px;
}


@media screen and (max-width:1050px)
{
  #addBtn,#btn
  {
    width:200px;
  }
}
@media screen and (max-width:700px)
{
  nav{
    width:100%;
    height: 50vh;
    float: none;
  }
  article
  {
    float:none;
    width:100%;
    height: 50vh;

  }
  #title
  {
    width:100%
  }
  #notes
  {
    width:100%;
    height: 100px;
  }
}